@mixin nav_active {
  position: relative;
  &::after {
    opacity: 0;
    content: '';
    position: absolute;
    bottom: -0.4em;
    left: 0.4em;
    right: 0.4em;
    height: 0.15em;
    background-color: var(--themeColor);
  }

  &.on {
    font-weight: bold;
    &::after {
      opacity: 1;
    }
  }
}

@mixin ellipse($line: 2) {
  display: -webkit-box;
  /*! autoprefixer: ignore next */
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $line;
  overflow: hidden;
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin select-nav($width: 80px) {
  .nav {
    flex: none;
    border-radius: 15px;
    display: flex;
    border: 1px solid #BBBBBB;
    margin: 10px auto;
    width: min-content;

    &_link {
      position: relative;
      flex: none;
      width: $width;
      height: 30px;
      line-height: 30px;
      text-align: center;
      border-radius: 15px;

      &:hover {
        background-color: #F5F5F5;
      }

      &.on {
        background-color: #BBBBBB;
        color: #fff;
      }

      .lt_ico {
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(20%, -50%);
        border: 1px solid #fff;
        border-radius: 1000px;
        background-color: var(--themeColor);
        line-height: 1;
        padding: 2px 4px;
        color: #fff;
      }
    }
  }
}

.ui_contextmenu {
  position: absolute;
  width: 224px;
  z-index: 50;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.33);
  border: 1px solid #C7C7C7;
  padding: 2px;
  &.sub {
    display: none;
    top: 0;
    left: 99%;
    &:hover {
      display: block;
    }
  }
  &_item {
    position: relative;
  }

  &_btn {
    user-select: none;
    display: flex;
    height: 36px;
    line-height: 36px;
    width: 100%;
    display: flex;
    align-items: center;
    &:hover {
      background-color: #EDEDEF;
    }

    .ico {
      width: 40px;
      padding-left: 10px;
    }
    &:hover {
      & + .sub {
        display: block;
      }
    }
  }

  .arrow {
    margin-left: auto;
    margin-right: 4px;
    border-color: transparent;
    border-width: 4px 8px;
    border-style: solid;
    border-left-color: #000;
  }
}
